import languageConf from './language';
import { ObjectIndex } from "./ObjectIndex";

//phpæµç¨‹æ§åˆ¶å…³é”®å­— - control-structures
const structuresArr:ObjectIndex = {
	"if"      : "if",
	"else"    : "else",
	"elseif"  : "elseif",
	"while"   : "while",
	"do"      : "do.while",
	"for"     : "for",
	"foreach" : "foreach",
	"break"   : "break",
	"continue": "continue",
	"switch"  : "switch",
	"declare" : "declare",
};

//oopå…³é”®å­— - language.oop5
const oopArr:ObjectIndex = {
	"class"      : "basic",
	"$this"      : "basic",
	"new"        : "basic",
	"const"      : "constants",
	"--construct": "decon",
	"public"     : "visibility",
	"protected"  : "visibility",
	"private"    : "visibility",
	"extends"    : "inheritance",
	"::"         : "paamayim-nekudotayim",
	"static"     : "static",
	"abstract"   : "abstract",
	"interfaces" : "interfaces",
	"trait"      : "trait",
	"anonymous"  : "anonymous",
	"--set"      : "overloading",
	"--get"      : "overloading",
	"--isset"    : "overloading",
	"--unset"    : "overloading",
	"final"      : "final",
	"--clone"    : "cloning",
};

//å‘½åç©ºé—´ç›¸å…³ - language.namespaces
const namespaceArr:ObjectIndex = {
	"use"      : "importing",
	"namespace": "rationale",
}

//functionç›¸å…³ - functions
const functionsArr:ObjectIndex = {
	"function" : "user-defined"
}

//å˜é‡ç±»å‹ - language.types
const typeArr:ObjectIndex = {
	"true"  : "boolean",
	"false" : "boolean",
	"int"   : "integer",
	"null"  : "null",
	"object": "object",
	"float" : "float",
	"double": "float",
	"real"  : "float",
};

//å…¨å±€å˜é‡å…³é”®å­— - reserved.variables
const globalsArr:ObjectIndex = {
	"$GLOBALS"             : "globals",
	"$-SERVER"             : "server",
	"$-GET"                : "get",
	"$-POST"               : "post",
	"$-FILES"              : "files",
	"$-REQUEST"            : "request",
	"$-SESSION"            : "session",
	"$-ENV"                : "environment",
	"$-COOKIE"             : "cookies",
	"$php_errormsg"        : "phperrormsg",
	"$HTTP-RAW-POST-DATA"  : "httprawpostdata",
	"$http-response-header": "httpresponseheader",
	"$argc"                : "argc",
	"$argv"                : "argv",
};

export default (lan:string,func:string):string => {
	//è·å–æ–‡æ¡£è¯­è¨€ç±»å‹
	let config_language:string = languageConf(lan);
	//å…³é”®å­—ç±»å‹
	let func_type:string = "function";//é»˜è®¤ä¸ºå‡½æ•°
	//æ£€æŸ¥æ˜¯å¦ä¸ºphpæµç¨‹æ§åˆ¶å…³é”®å­—
	let isStructrues:boolean = structuresArr[func] ? true:false;
	//åˆ¤æ–­å…³é”®å­—ç±»å‹
	if(isStructrues){
		return getUrl(config_language,structuresArr[func],"control-structures");
	}

	//æ£€æŸ¥æ˜¯å¦ä¸ºfunctionså…³é”®å­—
	let isFunctions:boolean = functionsArr[func] ? true:false;
	if(isFunctions){
		return getUrl(config_language,functionsArr[func],"functions");
	}

	//æ£€æŸ¥æ˜¯å¦ä¸ºoopå…³é”®å­—
	let isOOP:boolean = oopArr[func] ? true:false;
	if(isOOP){
		return getUrl(config_language,oopArr[func],"language.oop5");
	}

	//æ£€æŸ¥æ˜¯å¦ä¸ºå…¨å±€å˜é‡å…³é”®å­—
	let isGlobals = globalsArr[func] ? true:false;
	if(isGlobals){
		return getUrl(config_language,globalsArr[func],"reserved.variables");
	}

	//æ£€æŸ¥æ˜¯å¦ä¸ºå‘½åç©ºé—´å…³é”®å­—
	let isNamespace:boolean = namespaceArr[func] ? true:false;
	if(isNamespace){
		return getUrl(config_language,namespaceArr[func],"language.namespaces");
	}

	//æ£€æŸ¥æ˜¯å¦ä¸ºå˜é‡ç±»å‹å…³é”®å­—
	let isTypes = typeArr[func] ? true:false;
	if(isTypes){
		return getUrl(config_language,typeArr[func],"language.types");
	}

	return getUrl(config_language,func,func_type);
	
}

//è·å–phpæ–‡æ¡£url
function getUrl(language:string,func:string,func_type:string):string{

	return `https://php.net/manual/${language}/${func_type}.${func}.php`;

}